'use client';

import { useState, useEffect } from 'react';
import { Users } from 'lucide-react';
import NavBar from '@/components/NavBar';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from '@/components/ui/accordion';
import { Card, CardContent } from '@/components/ui/card';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { getData } from '@/lib/data';
import Image from 'next/image';

export default function EquiposPage() {
  const [category, setCategory] = useState<'Masculino' | 'Femenino'>('Masculino');
  const [data, setData] = useState<any>(null);

  useEffect(() => {
    async function loadData() {
      const tournamentData = await getData();
      setData(tournamentData);
    }
    loadData();
  }, []);

  if (!data) {
    return (
      <div className="min-h-screen bg-background">
        <NavBar />
        <div className="container mx-auto px-4 py-12">
          <p className="text-center">Cargando...</p>
        </div>
      </div>
    );
  }

  const getInitials = (name: string) => {
    return name
      .split(' ')
      .map((n) => n[0])
      .join('')
      .toUpperCase();
  };

  return (
    <div className="min-h-screen bg-background">
      <NavBar />

      <div className="container mx-auto px-4 py-12">
        <div className="mb-8 animate-fade-in">
          <h1 className="text-5xl font-bold text-primary mb-4 flex items-center gap-3">
            <Users className="h-12 w-12 text-secondary" />
            Equipos
          </h1>
          <p className="text-xl text-muted-foreground">
            Conoce todos los equipos y sus integrantes
          </p>
        </div>

        <Tabs defaultValue="Masculino" className="animate-scale-in">
          <TabsList className="grid w-full max-w-md grid-cols-2 mb-8">
            <TabsTrigger value="Masculino" onClick={() => setCategory('Masculino')}>
              Masculino
            </TabsTrigger>
            <TabsTrigger value="Femenino" onClick={() => setCategory('Femenino')}>
              Femenino
            </TabsTrigger>
          </TabsList>

          <TabsContent value="Masculino">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {data.equipos.map((equipo: any) => (
                <Card key={equipo.id} className="hover:shadow-lg transition-all">
                  <CardContent className="p-6">
                    <div className="flex flex-col items-center text-center gap-4">
                      {equipo.escudo_url ? (
                        <Image
                          src={equipo.escudo_url}
                          alt={equipo.nombre}
                          width={80}
                          height={80}
                          className="object-contain"
                        />
                      ) : (
                        <div className="w-20 h-20 bg-primary rounded-full flex items-center justify-center">
                          <Users className="h-10 w-10 text-secondary" />
                        </div>
                      )}
                      <div>
                        <h3 className="text-2xl font-bold text-primary">{equipo.nombre}</h3>
                        {equipo.grupo && (
                          <p className="text-sm text-muted-foreground mt-1">
                            Grupo {equipo.grupo}
                          </p>
                        )}
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </TabsContent>

          <TabsContent value="Femenino">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {data.equipos.map((equipo: any) => (
                <Card key={equipo.id} className="hover:shadow-lg transition-all">
                  <CardContent className="p-6">
                    <div className="flex flex-col items-center text-center gap-4">
                      {equipo.escudo_url ? (
                        <Image
                          src={equipo.escudo_url}
                          alt={equipo.nombre}
                          width={80}
                          height={80}
                          className="object-contain"
                        />
                      ) : (
                        <div className="w-20 h-20 bg-primary rounded-full flex items-center justify-center">
                          <Users className="h-10 w-10 text-secondary" />
                        </div>
                      )}
                      <div>
                        <h3 className="text-2xl font-bold text-primary">{equipo.nombre}</h3>
                        {equipo.grupo && (
                          <p className="text-sm text-muted-foreground mt-1">
                            Grupo {equipo.grupo}
                          </p>
                        )}
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}


